.header-wrapper
    background-color: #fff
    border-bottom: 1px solid #e8e8e8
    padding: 12px 0

.header
    display: flex
    margin: 0 24px
    .left
        flex: 1
        .title
            font-size: 18px
            margin-bottom: 0
            line-height: 1
    .main
        flex: 1
        text-align: center
    .right
        flex: 1
        text-align: right